import React, { useContext } from 'react';
import { 
  View, 
  Text, 
  TouchableOpacity, 
  StyleSheet, 
  SafeAreaView,
  StatusBar
} from 'react-native';
import { useAuth } from '../context/AuthContext';
import { useTheme } from '../context/ThemeContext';

const HomeScreen = ({ navigation }) => {
  const { user, logout } = useAuth();
  const theme = useTheme();

  const handleLogout = async () => {
    await logout();
    navigation.replace('Login');
  };

  return (
    <SafeAreaView style={[styles.container, { backgroundColor: theme.background }]}>
      <StatusBar barStyle={theme.text === '#fff' ? 'light-content' : 'dark-content'} />
      <View style={styles.content}>
        <Text style={[styles.welcomeText, { color: theme.text }]}>
          Welcome, {user?.username || 'Player'}!
        </Text>
        
        <View style={styles.menuContainer}>
          <TouchableOpacity
            style={[styles.menuButton, { backgroundColor: theme.primary }]}
            onPress={() => navigation.navigate('Game')}
          >
            <Text style={styles.buttonText}>Play Game</Text>
          </TouchableOpacity>
          
          <TouchableOpacity
            style={[styles.menuButton, { backgroundColor: theme.secondary }]}
            onPress={() => navigation.navigate('Statistics')}
          >
            <Text style={styles.buttonText}>View Statistics</Text>
          </TouchableOpacity>
          
          <TouchableOpacity
            style={[styles.menuButton, { backgroundColor: theme.card }]}
            onPress={() => navigation.navigate('Settings')}
          >
            <Text style={[styles.buttonText, { color: theme.text }]}>Settings</Text>
          </TouchableOpacity>
          
          <TouchableOpacity
            style={[styles.menuButton, { backgroundColor: theme.error }]}
            onPress={handleLogout}
          >
            <Text style={styles.buttonText}>Log Out</Text>
          </TouchableOpacity>
        </View>
      </View>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  content: {
    flex: 1,
    padding: 20,
    justifyContent: 'center',
    alignItems: 'center',
  },
  welcomeText: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 40,
    textAlign: 'center',
  },
  menuContainer: {
    width: '100%',
    maxWidth: 300,
  },
  menuButton: {
    height: 50,
    borderRadius: 8,
    justifyContent: 'center',
    alignItems: 'center',
    marginBottom: 15,
  },
  buttonText: {
    color: '#fff',
    fontSize: 16,
    fontWeight: 'bold',
  },
});

export default HomeScreen; 