import React, { useContext, useEffect, useState } from 'react';
import { View, Text, Button, StyleSheet, Alert, TouchableOpacity, ScrollView, Share, Linking } from 'react-native';
import { GameContext } from '../context/GameContext';
import { StatsContext } from '../context/StatsContext';
import { useTheme } from '../context/ThemeContext';
import { AuthContext } from '../context/AuthContext';

const GameOverScreen = ({ navigation }) => {
  const { score, gameStartTime, resetGame, startGame, successfulWords, getGameUrl } = useContext(GameContext);
  const { updateStatsAfterGame } = useContext(StatsContext);
  const theme = useTheme();
  const { user } = useContext(AuthContext);

  const [duration, setDuration] = useState(0);

  useEffect(() => {
    if (gameStartTime) {
      const now = Date.now();
      const elapsed = Math.floor((now - gameStartTime) / 1000);
      setDuration(elapsed);
      updateStatsAfterGame({ score, duration: elapsed });
    }
  }, [gameStartTime]);

  const getFormattedTime = () => {
    const minutes = String(Math.floor(duration / 60)).padStart(2, '0');
    const seconds = String(duration % 60).padStart(2, '0');
    return `${minutes}:${seconds}`;
  };

  const handlePlayAgain = () => {
    resetGame();
    startGame();
    navigation.navigate('Game');
  };

  const handleBackToMenu = () => {
    resetGame();
    if (user) {
      navigation.navigate('Home');
    } else {
      navigation.navigate('Login');
    }
  };

  const handleShare = async (platform) => {
    try {
      // Create a formatted message with score, words, and game URL
      const gameUrl = getGameUrl();
      const wordList = successfulWords.map(item => item.word).join(', ');
      const totalWords = successfulWords.length;
      
      let message = `I scored ${score} points in Word Game! 🎮\n\n`;
      message += `I found ${totalWords} words: ${wordList}\n\n`;
      message += `Play the game here: ${gameUrl}`;
      
      // Use React Native's Share API
      const result = await Share.share({
        message,
        title: 'My Word Game Score',
      });
      
      if (result.action === Share.sharedAction) {
        if (result.activityType) {
          console.log(`Shared via ${result.activityType}`);
        } else {
          console.log('Shared successfully');
        }
      } else if (result.action === Share.dismissedAction) {
        console.log('Share dismissed');
      }
    } catch (error) {
      Alert.alert('Error', 'Failed to share score');
      console.error(error);
    }
  };

  return (
    <View style={[styles.container, { backgroundColor: theme.background }]}>
      <Text style={[styles.title, { color: theme.text }]}>Game Over</Text>
      <Text style={[styles.score, { color: theme.text }]}>Final Score: {score}</Text>
      <Text style={[styles.time, { color: theme.border }]}>Total Time: {getFormattedTime()}</Text>
      
      <View style={styles.wordsContainer}>
        <Text style={[styles.wordsTitle, { color: theme.text }]}>Words Found ({successfulWords.length})</Text>
        <ScrollView style={styles.wordsList}>
          {successfulWords.length > 0 ? (
            successfulWords.map((item, index) => (
              <View key={index} style={styles.wordItem}>
                <Text style={[styles.wordText, { color: theme.text }]}>{item.word}</Text>
                <Text style={[styles.pointsText, { color: theme.highlight }]}>{item.points} pts</Text>
              </View>
            ))
          ) : (
            <Text style={[styles.noWords, { color: theme.border }]}>No words found</Text>
          )}
        </ScrollView>
      </View>

      <View style={styles.shareSection}>
        <Text style={[styles.shareTitle, { color: theme.text }]}>Share My Score</Text>
        <View style={styles.shareButtons}>
          <TouchableOpacity 
            style={[styles.shareButton, { backgroundColor: theme.card }]} 
            onPress={() => handleShare('Facebook')}
          >
            <Text style={[styles.shareButtonText, { color: theme.highlight }]}>Share</Text>
          </TouchableOpacity>
        </View>
      </View>

      <View style={styles.buttonContainer}>
        <Button title="Play Again" onPress={handlePlayAgain} />
        <Button title="Back to Menu" onPress={handleBackToMenu} />
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 24,
    justifyContent: 'center',
    alignItems: 'center',
  },
  title: {
    fontSize: 36,
    marginBottom: 20,
  },
  score: {
    fontSize: 24,
    marginBottom: 10,
  },
  time: {
    fontSize: 20,
    marginBottom: 30,
  },
  wordsContainer: {
    width: '100%',
    marginBottom: 20,
    alignItems: 'center',
  },
  wordsTitle: {
    fontSize: 18,
    fontWeight: 'bold',
    marginBottom: 10,
  },
  wordsList: {
    width: '100%',
    maxHeight: 200,
  },
  wordItem: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    paddingVertical: 5,
    paddingHorizontal: 10,
    borderBottomWidth: 1,
    borderBottomColor: 'rgba(0,0,0,0.1)',
  },
  wordText: {
    fontSize: 16,
  },
  pointsText: {
    fontSize: 16,
    fontWeight: 'bold',
  },
  noWords: {
    fontSize: 16,
    fontStyle: 'italic',
    textAlign: 'center',
    marginTop: 10,
  },
  buttonContainer: {
    gap: 12,
    width: '100%',
    paddingHorizontal: 20,
    marginTop: 24,
  },
  shareSection: {
    marginBottom: 20,
    alignItems: 'center',
  },
  shareTitle: {
    fontSize: 18,
    fontWeight: 'bold',
    marginBottom: 10,
  },
  shareButtons: {
    flexDirection: 'row',
    justifyContent: 'center',
    gap: 10,
  },
  shareButton: {
    paddingVertical: 8,
    paddingHorizontal: 20,
    borderRadius: 5,
  },
  shareButtonText: {
    fontSize: 16,
    fontWeight: 'bold',
  },
});

export default GameOverScreen;
